import React from "react";

const Suscribe = () => {
  return (
    <div className="suscribe flex-row">
      <input type="text" placeholder="email@example.com" />
      <div className="suscribe-button">SUSCRIBE</div>
    </div>
  );
};

export default Suscribe;



